:root {
  --font-f__main: "Nunito", sans-serif;
  --font-f__h: "Source Serif Pro", serif;
  --font-f__quote: "Caveat", cursive;
  --font-s__main: 24px;
  --color__main: #1d2a3b;
  --color__secondary: #495c77;
  --color__w: #fff;
  --color__accent: #684ab8;
  --color__hover: #684ab8bf;
  --color-bg__main: #f0faff;
  --color-bg__header: #000000b3;
  --shadow__main: 0 0 4px #0003;
  --items: 1;
  --gap: 24px;
  --width: 100%;
  --height__header: 44px;
  --border__radius: 4px;
  --transition__main: 1s cubic-bezier(.4, 0, .2, 1);
  --transition__hover: .4s cubic-bezier(.4, 0, .2, 1);
}

*, :before, :after {
  box-sizing: border-box;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

:focus, :active, a:focus, a:hover {
  outline: none;
}

nav, footer, header {
  display: block;
}

a, button, input, textarea {
  color: inherit;
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-f__main);
  letter-spacing: .03em;
  color: var(--color__main);
  font-size: 14px;
  line-height: 1.5;
  position: relative;
}

body.mobile-menu-is-open, body.isLoading {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
