  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop, .basicLightbox__placeholder {
  z-index: 10000;
  background-color: #000000f5;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

.backdrop.isShow, .basicLightbox__placeholder.isShow {
  display: flex;
}

.backdrop__loader, .basicLightbox__placeholder {
  z-index: 10000;
  background-color: #000000f5;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.backdrop__loader.hide, .basicLightbox__placeholder.hide {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid #0000;
  border-radius: 50%;
  animation: 1s linear infinite rotation;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid #0000;
  border-left: 4px solid #684ab8;
  border-radius: 50%;
  animation: .5s linear infinite reverse rotation;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

